(function() {
    'use strict';

    angular
        .module('core.app')
        .controller('MainController', MainController);

    /** @ngInject */
    function MainController() {
        var vm = this;
        vm.techs = [{
            title: 'MongoDB',
            desc: 'Banco de dados orientado a documentos - NoSQL',
            logo: 'mongodb.png'
        }, {
            title: 'Express',
            desc: 'Framework para Node.js. Flexível e robusto para desenvolver aplicações web.',
            logo: 'express.png'
        }, {
            title: 'AngularJS',
            desc: 'Framework Javascript para criação de aplicações single-page',
            logo: 'angular.png'
        }, {
            title: 'Node.js',
            desc: 'Plataforma baseada em Javascript para construir aplicações de rede rápidas e escaláveis',
            logo: 'node.png'
        }];
    }
})();
